.todo{
    display: flex;
    /* flex-wrap: wrap; */
}
.todo >*{
    margin: 0.5em 0em;
}
.title{
    width: 60%;
    word-wrap: break-word;
    text-overflow: clip;
    border: 2px solid black;
}
.strikeTitle{
    width: 60%;
    text-decoration:  2px line-through;
}
.createdAt{
    width: 30%;
    border: 2px solid black;
}
.btn{
    background-color: none;
    border: 2px solid black;
}
